* {
  margin: 0;
  padding: 0;
}

header {
  height: 75px;
  width: auto;
  background-color: black;
  display: flex;
  justify-content: space-around;
  align-items: center;
}

header a {
  color: antiquewhite;
  text-decoration: none;
  font-size: 24px;
}

.glider img {
  height: 100vh !important;
  object-fit: cover;
  /* To maintain the image aspect ratio and cover the container */
}

.about-container {
  padding: 3%;
  background: radial-gradient(circle, rgba(63, 94, 251, 1) 0%, rgba(252, 70, 107, 1) 100%);
}

.aboutus {
  text-align: center;
  background: linear-gradient(to right, #ff0000, #777788);
  -webkit-background-clip: text;
  color: transparent;
}

.about-container p{
  line-height: 1.8rem;
  letter-spacing: 1px;
  word-spacing: 5px;
  font-size: 16px;
  color: white;
}